.character__list--container-all {
  background-color: rgb(12, 12, 12);
  background-size: cover;
  overflow: hidden;
  position: relative;
  padding-bottom: 80px;
  min-height: 70vh;
}

.character__list--animation {
  position: absolute;
  width: 600vw;
  min-height: 100%;
  background-size: cover;
  animation: slide 40s linear infinite;
  background: repeat;
}

@keyframes slide {
  0%{
    transform: translate3d(0, 0, 0);
  }
  100%{
    transform: translate3d(-1692px, 0, 0);
  }
}

.character__list--container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.character__list {
  list-style: none;
  padding-left: 0;
  text-decoration: none;
  color:black;
  padding: 20px 0;

  @media all and (min-width: 500px) {
    display: grid;
    grid-template-columns: 170px 170px;
    grid-gap: 40px;
  }

  @media all and (min-width: 730px) {
    grid-template-columns: 170px 170px 170px;
  }

  @media all and (min-width: 950px) {
    grid-template-columns: 170px 170px 170px 170px;
  }

  @media all and (min-width: 1200px) {
    grid-template-columns: 170px 170px 170px 170px 170px;
  }

}

.character__list--link {
  text-decoration: none;
  color: black;
  font-family: 'Montaga', serif;
}

.character__list--item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  padding-bottom: 20px;
  @media all and (min-width: 500px) {
    padding-bottom: 0;
  }
}

.character__list--name-container {
  height: 65px;
  width: 210px;
  background-position: center;
  background-size: cover;
  position: absolute;
  top: 62%;
  @media all and (min-width: 500px) {
    top: 64%;
  }
}

.character__list--name {
  font-size: 15px;
  margin: 25px 0 8px 0;
  text-align: center;
  transform: rotate(4deg);
}

.character__list--image-bg {
  height: 260px;
  width: 180px;
  background-size: cover;
  background-position: center;
}

.character__list--house {
  height: 65px;
  margin-top: 10px;
}

.character__list--house:hover{
  transform: scale(1.2);
}
